@import (inline ) "./ol.css";
@import (inline ) "./ol-popup.css";

@headerHeight: 80px;
@transitionTime: 0.2s;

@iconImg: 'data:image/png;base64,';
@rulerIcon: 'data:image/png;base64,';
@crashDownloadIcon: 'data:image/svg+xml;base64,';
@selectNone: 'data:image/png;base64,';
@venn: 'data:image/png;base64,';
@globeIcon: 'data:image/png;base64,';
@clusterToggle: 'data:image/png;base64,';

.ol-zoom {
  left: 32px;
}

header {
  display: flex;
  background: rgb(206, 206, 206);
  background: linear-gradient(90deg, rgba(203, 32, 40, 1) 0%, rgba(203, 32, 40, 1) 49%, rgb(246, 246, 246) 100%);
  img {
    margin: 4px;
  }

  #title-bar {
    flex: 1;
    padding-left: 15px;

    h2 {
      margin: 7px 0;
      color: white;
    }
  }
}

html, body, #root, #app-container {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

header {
  height: @headerHeight;
}

#app-container {

  #map-container {
    // check value of @headerHeight: 80px;
    height: ~"calc(100% - 80px)";;
    display: flex;

    > div {
      height: 100%;
    }
  }
}

#accordion-container {
  width: 350px;
  position: relative;
  transition: width @transitionTime;

  #hider {
    position: absolute;
    right: -51px;
    z-index: 5;
    transform: rotate(90deg);
    top: 21px;
    background-color: #CB2028;
    border: solid #9c1d22 1px;
    padding: 5px 12px;
    border-top-right-radius: 7px;
    color: white;
    cursor: pointer;
  }

  #accordion {

    > h3 {
      margin: 0;
      padding: 6px;
      border-bottom: none;
    }

    > h3:first-of-type {
      border-top-right-radius: 0;
    }

    > div {
      padding: 5px;
      border-bottom: none;
    }

    .ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active, a.ui-button:active, .ui-button:active, .ui-button.ui-state-active:hover {
      background-color: #CB2028;
      border-color: #9c1d22;
    }
  }
}

#accordion-container.collapsed {
  width: 0;

  #hider {
    display: none;
  }
}

#accordion-container-collapsed {
  //display: none;
  background-color: #CB2028;
  transition: width @transitionTime;
  position: relative;
  width: 28px;

  #shower {
    left: -26px;
    top: 24px;
    position: absolute;
    border: solid #9c1d22 2px;
    color: white;
    padding: 4px 10px;
    cursor: pointer;
    transform: rotate(90deg);
  }
}

#accordion-container-collapsed.collapsed {
  width: 0;

  #shower {
    display: none;
  }
}

#unmapped-list {
  //background-color: #808080;
  span {
    display: inline-block;
    margin-right: 6px;
    color: blue;
    text-decoration: underline;
    cursor: pointer;

    a {
      margin-left: 4px;
    }

  }
}

.about-div ul {
  padding-left: 17px;
}

#map {
  height: 100%;
  flex: 1;
  position: relative;

  #crash-info {
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 5;
    height: 200px;
    width: 300px;
    background-color: white;
    overflow: auto
  }
}

.legend-container {
  display: flex;
  flex-direction: column;

  //> div {
  //  //border-bottom: solid black 1px;
  //  min-height: 50px;
  //  flex: 1;
  //  overflow-y: auto;
  //}

}

#legend-container-outer, #selections {
  border-bottom: solid black 1px;
}

#legend-container-outer{
  height: 160px;
}

.legend-table-container {
  //border-bottom: solid black 1px;
  flex: 1;
  overflow-y: auto;
}

#selection-container {
  display: flex;
  flex-direction: column;
  padding: 0;
  flex: 2;

  > div {
