#uunmapped-list {
  border-bottom: solid black 1px;
}

#toolbar {
  position: absolute;
  left: 0;
  top: 100px;
  //height: 35px;
  //width: 200px;
  background-color: white;
  z-index: 5;

  #selection-operation {
    display: inline-block;
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      margin-left: 15px;
    }
  }

  .toolbar-button {
    display: block;
    height: 25px;
    width: 25px;
    background: url(@iconImg) no-repeat;
    border: solid black 1px;
    cursor: pointer;
    padding: 2px;
    margin: 1px;
  }

  .toolbar-button:hover {
    background-color: lightgray;
  }

  .ruler {
    background: url(@rulerIcon) no-repeat;
    margin-top: 15px;
  }

  .zoom-extent {
    background: url(@globeIcon) no-repeat;
    background-position: 3px 3px;
    margin-bottom: 7px;
  }

  .clear-selection {
    background: url(@selectNone) no-repeat;
    background-size: 25px 25px;
    background-position: 2px 2px;
  }

  .toggle-cluster {
    background: url(@clusterToggle) no-repeat;
    background-size: 57px 25px;
    //background-position: 2px 2px;
  }

  .selection-mode {
    background: url(@venn) no-repeat;
    background-size: 124px;
    display: inline-block;
  }
}

