@search-height: 25px;

#search-bar-div {
  position: absolute;
  z-index: 4;
  top: 12px;
  left: 75px;

  #search-bar-text {
    height: 25px;
    border-radius: 5px 0 0 5px;
    border: solid black 1px;
    padding-left: 5px;
    width: 200px;
    vertical-align: top;
  }

  #search-button {
    height: 29px;
    width: 35px;
    display: inline-block;
    padding: 3px;

    //background-color: white;
    border-radius: 0 5px 5px 0;
    background: url('data:image/png;base64,') no-repeat;
    background-size: 21px 21px;
    background-position: 5px 3px;
    border: solid black 1px;
    cursor: pointer;
    background-color: white;

  }

  #search-button:hover {
    background-color: lightgray;
  }
}

.crash-download {
  background: url(@crashDownloadIcon) no-repeat;
  width: 18px;
  height: 18px;
  display: inline-block;
}

#dummy {
  color: orange;
  cursor: not-allowed;
}

.ol-popup {
  cursor: default;
  //min-height: 400px;
}

.multipop-header {
  margin-top: 7px;
  padding: 3px 0 3px 3px;
  background-color: lightblue;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;

  span {
    cursor: pointer;
    padding: 0 3px;
  }
}

#popup-content {
  height: 250px;
  overflow-y: auto;
  margin-top: 9px;
}

.crash-attribute-table {
  border-collapse: collapse;
  width: 262px;

  td {
    border: solid black 1px;
  }
}

.tooltip {
  position: relative;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  color: white;
  padding: 4px 8px;
  opacity: 0.7;
  white-space: nowrap;
}

.tooltip-measure {
  opacity: 1;
  font-weight: bold;
}

.tooltip-static {
  background-color: #ffcc33;
  color: black;
  border: 1px solid white;
}

.tooltip-measure:before,
.tooltip-static:before {
  border-top: 6px solid rgba(0, 0, 0, 0.5);
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
  content: "";
  position: absolute;
  bottom: -6px;
  margin-left: -7px;
  left: 50%;
}

.tooltip-static:before {
  border-top-color: #ffcc33;
}

.hidden-help-tooltip {
  display: none;
}

#help-div {
